  font-weight: 600;
  line-height: 16px;
  transition: background-color .3s;
}

.main-btn:hover {
  background-color: #af3419;
}

.main-btn:active {
  background-color: #ea340d;
}

.main-btn:disabled {
  cursor: not-allowed;
  opacity: .5;
}

.secondary-btn {
  color: #cd4d31;
  white-space: nowrap;
  background-color: #f1f5f4;
  border: 1px solid #cd4d31;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 100%;
  max-height: 60px;
  padding-top: 22px;
  padding-bottom: 22px;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  transition: border .3s;
  display: -ms-flexbox;
  display: flex;
}

.secondary-btn:after {
  content: "";
  background-image: url("orange-arrow.0f5033e1.svg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 20px;
  height: 20px;
  display: block;
}

.secondary-btn:hover {
  color: #af3419;
  border: 1px solid #af3419;
}

.secondary-btn:hover:after {
  background-image: url("dark-orange-arrow.25675455.svg");
}

.secondary-btn:active {
  color: #ea340d;
  border: 1px solid #ea340d;
}

.secondary-btn:active:after {
  background-image: url("light-orange-arrow.ac0768e4.svg");
}

.secondary-btn:disabled {
  cursor: not-allowed;
  opacity: .5;
}

.lang-btn {
  text-transform: uppercase;
  color: #f1f5f4;
  opacity: 0;
  pointer-events: none;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  position: relative;
}

@media (width >= 640px) {
  .lang-btn {
    pointer-events: auto;
    opacity: 1;
  }
}

.lang-btn span {
  background-color: #f1f5f4;
  width: 4.6px;
  height: 1px;
  display: block;
  position: absolute;
  top: 50%;
  right: -13px;
  transform: translateY(-50%);
}

.lang-btn span:first-child {
  right: -13px;
  transform: rotate(45deg);
}

.lang-btn span:nth-child(2) {
  right: -16px;
  transform: rotate(-45deg);
}

.burger {
  width: 30px;
  height: 30px;
  padding: 5px 3px;
  position: relative;
}

.burger span {
  background-color: #f1f5f4;
  width: 25px;
  height: 2px;
  display: block;
}

.burger span:first-child {
  position: absolute;
  top: 5px;
}

.burger span:nth-child(2) {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.burger span:nth-child(3) {
  position: absolute;
  bottom: 5px;
}

.mobile-menu {
  color: #f1f5f4;
  z-index: 10;
  transform-origin: 100% 0;
